/*--溢出文字點點點--*/
@mixin over_text($line) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
}

/*--隱藏區塊內文字--*/
@mixin text-indent {
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
}
/* --------------按鈕設定開始--------------- */
@mixin btn-dark($bgcolor) {
    color: $white !important;
    background: $bgcolor!important;
    border: 1px solid $bgcolor;
    padding: 4px 16px;
    border-radius: 4px;
    &:hover {
        color: $white!important;
        background: darken($bgcolor, 5%)!important;
    }
}
@mixin btn-border($color) {
    color: $color !important;
    border: 1px solid lighten($color: $color, $amount: 30%);
    padding: 4px 16px;
    border-radius: 4px;
    position: relative;
    &:hover {
        color: darken($color, 5%)!important;
        box-shadow: none;
    }
    .btn-id{
        width: 31px;
        height: 31px;
        display: flex;
        padding-top: 3px;
        justify-content: center;
        position: absolute;
        left: 16px;
        top: 6px;
        border-radius: 50%;
        color: $white;
        @include Pad{
            padding-top: 4px;
        }
    }
}
