/*------------漸層設定開始------------*/
@mixin gradient-horizon($colorstart, $colorend) {
    background: $colorstart;
    /* Old browsers */
    background: -moz-linear-gradient(left, $colorstart 0%, $colorend 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, $colorstart 0%, $colorend 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, $colorstart 0%, $colorend 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
@mixin gradient-vertical($colorstart, $colorend) {
    background: $colorstart;
    /* Old browsers */
    background: -moz-linear-gradient(top, $colorstart 0%, lighten($colorstart,5%) 30%, $colorend 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, $colorstart 0%, lighten($colorstart,5%) 30%, $colorend 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, $colorstart 0%, lighten($colorstart,5%) 30%, $colorend 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

