
/*--------------------landing page 開始------------------*/
.main{
    &-title{
        font-size: $font-lg;
        font-weight: $font-heavy;
        color: $white;
    }
    &-subtitle{
        display: inline-block;
        font-size: $font-sm;
        background: $primary-dark-color;
        border-radius: 32px;
        padding: 4px 16px;
        color: $primary-xlight-color;
    }
}
/*--------------------首頁開始------------------*/
.top{
    margin-top: -32px;
    position: relative;
    z-index: 10;
    &-title {
            background: $primary-color;
            border-radius: $w-md $w-md 0 0;
            color: $white;
            font-size: $font-md;
            padding: $w-xs $w-sm;
            text-align: center;
        }
    &-body{
        background: $primary-xxlight-color;
        border-radius: 0 0 $w-md $w-md;
        padding: $w-xs $w-sm;
        font-size: $font-xsm;
    }
}
.time-limit{
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: $font-heavy;
    color: $primary-color;
    span{
        font-size: $font-xxl;
        margin-top: -2px;
    }
}

/*--------------------主選單開始------------------*/
.navbar {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    box-shadow: 0 0 8px $gray500;
    border-top: 1px solid $gray400;
    border-bottom: 1px solid $gray400;
    position: sticky;
    top: 0;
    z-index: 100;
    &-btn{
        flex-shrink: 0;
        width: 37px;
        height: 54px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        right: 0;
        top: 0;
        z-index: 10;
        padding: 0;
        border: none;
        img{
            transition: all 0.2s ease;
        }
    }
    &.show{
        .nav{
            flex-wrap: wrap;
            padding: 12px 16px 4px 16px;
            &-item{
                margin-bottom: 8px;
            }
        }
        .navbar-btn{
            img{
                transform: rotate(180deg);
            }
        }
    }
}

.navbar-brand {
    width: 228px;

    h1 {
        font-size: $font-xxl;
    }
}
.nav {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    padding: 12px 16px 8px 16px;
    transition: all 0.2s ease;
    &-item{
        flex-shrink: 0;
        transition: all 0.2s ease;
    }
    &-link {
        color: $primary-color;
        padding: 4px 10px;
        font-size: $font-xs;
        font-weight: $font-bold;
        border-radius: 40px;
        border: 1px solid $primary-xlight-color;
        margin-right: 4px;
        transition: all 0.2s ease;
        &.active {
            background: $primary-color !important;
            border: 1px solid $primary-color;
            color: $white !important;
            position: relative;
        }

        &:hover {
            color: $primary-color;
        }
    }
}



.menu-card{
    overflow-y: auto;
    height: calc(100vh - 230px);
    &-pic{
        margin-bottom: $w-xxs;
        border-radius: $w-xxxs;
        flex: 0 0;
        img{
            border-radius: $w-xxxs;
            width: 100%;
        }
    }
    &-info{
        flex: 0 0;
        .search-input{
            display: none;
        }
    }
    &-title{
        flex: 1 1;
    }
    li{
        transition: all 0.2s ease;
    }
}
.menu-card-item{
    padding: 0 $w-sm $w-sm $w-sm;
    position: relative;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    color: $gray900 !important;
    height: 100%;
    &:after{
        content:"";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: calc(100% - #{$w-md});
        background: $gray100;
        border-radius: $w-xxxs;
        transition: all 0.2s ease;
    }
    &:hover{
        &:after{
            background: $white;
        }
    }
    &>*{
        position: relative;
        z-index: 3;
    }
}
.soldout{
    pointer-events: none;
    .menu-card-pic{
        background: $gray900;
        img{
            opacity: 0.5;
        }
        &::before{
        content:"售完";
        display: inline-block;
        position: absolute;
        background: $gray900;
        z-index: 3;
        background: $gray900;
        color: $white;
        padding: $w-xxxs $w-sm;
        font-weight: $font-xlight;
        border-radius: $w-xxxs;
        top: calc(50% - 17px);;
        left: calc(50% - 33px);
    }
    } 
}
.function-link-item{
    display: block;
    opacity: 0.3;
    padding: $w-xxxs;
    img{
        height: 12px;
    }
    &.active{
        opacity: 1;
        pointer-events: none;
    }
}