body {
	font-family: Noto Sans TC, 微軟正黑體;
}
.wrapper{
	zoom: 2.6;
	min-height: 100vh;
	padding-bottom: 88px;
}
.container{
	padding-left: 16px;
	padding-right: 16px;
}
.main{
	&[data-con="page"]{
        padding-top: 68px;
		position: relative;
        &:before{
            content: "";
			display: block;
			position: fixed;
			left: 0;
			top: 52px;
            width: 100%;
            height: 120px;
			z-index: -1;
            @include gradient-vertical($gray100,$white);
        }
        .main-pic{
            img{
				border-radius: $w-md;
			}
        }
    }
	&[data-con="check"]{
        padding-top: 68px;
		position: relative;
		background: $primary-color;
		&:before{
			position: absolute;
			content: "";
			display: block;
			width: calc(100% - 32px);
			height: 1px;
			left: 16px;
			top: 52px;
			background: $primary-xlight-color;
		}
	}
	&-pic {
		&[data-con="page"]{
			img{
				height: 120px;
			}
		}
		img {
			width: 100%;
			height: 230px;
		}
		&~p{
			font-size: $font-xsm;
		}
	}
	&-action{
		width: 100%;
		box-shadow: 0 -6px 24px $gray300;
		position: fixed;
		left: 0;
		bottom: 0;
		padding: 12px 0;
		z-index: 100;
		&[data-style="dark"]{
			box-shadow: none;
			background: $primary-color;
			.action-group-btn{
				background: none;
				border: 2px solid $white !important;
				color: $white;
			}
		}
		button{
			height: 48px;
			display: flex;
			justify-content: center;
			align-items: center;
			position: relative;
			font-weight: $font-bold;
			letter-spacing: 2px;
			font-size: $font-smd;
			span{
				position: absolute;
				right: 16px;
				top: 12px;
				font-size: $font-xsm;
				font-weight: $font-light;
				letter-spacing: normal;
			}
		}
	}
}
/*-----------------loading開始-------------------*/
.blockMsg {
	border: 0 !important;
	width: 80px !important;
	height: 80px !important;
	top: calc(50vh - 40px) !important;
	left: calc(50vw - 40px) !important;
	display: flex !important;
	justify-content: center !important;
	align-items: center !important;
	border-radius: $w-md !important;
	position: fixed !important;
	z-index: 1000;
}
/*--------------------菜單清單開始------------------*/
.menu-list {
	width: 100%;
	&-link{
		background: $white;
		border-radius: $w-xxxs;
		color: $gray900;
		display: flex;
		flex-direction: column;
		padding: $w-xxs;
		&:hover{
			color: $primary-color;
		}
	}
	&-item {
		position: relative;
		&:after {
			content: " ";
			display: block;
			width: 100%;
			height: 1px;
			background: $gray300;
			position: absolute;
			left: 0;
			bottom: 0;
		}
	}
	&-info {
		width: 100%;
	}
	&-pic{
		height: 60px;
		flex-shrink: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: relative;
	}
	&-number {
		position: absolute;
		display: flex;
		justify-content: center;
		min-width: 20px;
		height: 20px;
		right: -10px;
		top: -10px;
		background: $primary-color;
		color: $white;
		border-radius: 50px;
		padding: 0 4px;
		font-size: $font-xxs;
	}
	&-img {
		width: 100%;
		border-radius: $w-xxxs;
		overflow: hidden;
		object-fit: cover;
		margin-bottom: 4px;
	}
	&-title {
		font-size: .6rem;
		font-weight: $font-heavy;
		margin-bottom: 2px;
		letter-spacing: 0;
	}
	&-caption{
		display: block;
		font-size: $font-xxxs;
		color: $primary-color;
		font-weight: $font-heavy;
		margin-bottom: 4px;
	}
	&-txt {
		font-size: $font-xxxxs;
		color: $gray700;
		line-height: 1.1;
		font-weight: $font-bold;
	}
	&-remove {
		width: 66px;
		background: $danger-color;
		position: absolute;
		top: 0;
		right: -82px;
		color: $white;
		display: flex;
		justify-content: center;
		align-items: center;
		height: calc(100% - #{$w-sm});
	}
}
.number-form{
	input{
		border-top: 1px solid $primary-color;
		border-bottom: 1px solid $primary-color;
	}
	button{
		width: 40px;
		height: 40px;
		padding: 0;
	}
	button:first-child{
		border-radius: 5px 0 0 5px;
	}
	button:last-child{
		border-radius: 0 5px 5px ;
	}
}
/*--------------------訂單清單開始------------------*/
.order-list {
	width: 100%;
	&-item {
		position: relative;
		color: $gray900;
		display: flex;
		padding: $w-sm 0;
		&:after {
			content: " ";
			display: block;
			width: 100%;
			height: 1px;
			background: $gray300;
			position: absolute;
			left: 0;
			bottom: 0;
		}
		&:last-child{
			&:after{
				background: $primary-color;
			}
		}
	}
	&-info {
		width: 100%;
	}
	&-title {
		font-size: $font-sm;
		font-weight: $font-bold;
		margin-bottom: 0;
	}
	&-caption{
		font-size: $font-xxs;
	}
	&-txt {
		font-size: $font-xxs;
		color: $gray700;
	}
	&-end{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-end;
		flex-shrink: 0;
	}
	&-sum{
		font-size: $font-xs;
		&[data-con="check"]{
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: $font-xsm;
			color: $gray900;
			font-weight: $font-bold;
		}
	}
	.number-form{
		width: 125px;
		button{
			width: 31px;
		}
		button,input{
			height: 31px;
		}
	}
}
/*--------------------置底按鈕群組開始------------------*/
.action-group{
	display: flex;
	justify-content: space-between;
	padding: 0 !important;
	&-btn{
		background: $gray100;
		color: $gray900;
		font-size: $font-sm;
		font-weight: $font-bold;
		margin-right: 16px;
		width: 100%;
		padding: 8px 16px !important;
		border: none !important;
		border-radius: $w-xxxs;
		&:last-child{
			margin-right: 0;
		}
		&.active{
			background: $gray900;
			color: $white;
		}
	}
}
/*--------------------footer開始------------------*/
footer{
	font-size: $font-xxxxs;
	font-weight: $font-bold;
	margin-bottom: 86px;
}
.footer-landing{
	position: fixed;
	left: 0;
	bottom: 16px;
	margin-bottom: 0;
}
/*--------------------視窗開始------------------*/
.backdrop {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	display: none;
	z-index: 100;
	&.show {
		display: block;
	}
}
.modal {
	&-content {
		background: $primary-color;
		border-radius: $w-md;
		overflow: hidden;
	}
	&-header {
		border: 0;
		color: $white;
		display: flex;
		justify-content: center;
		position: relative;
		padding: 14px 16px;
		button {
			position: absolute;
			right: 10px;
			&:focus,
			&:active {
				border: none !important;
			}
		}
	}
	&-body {
		background: $white;
		font-size: $font-smd;
		min-height: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&-footer {
		background: $white;
		border: 0;
		display: flex;
		justify-content: center;
		&>*{
			width: 130px;
			margin: 0;
			flex-shrink: 1;
		}
	}
	&-action {
		border-radius: $w-xxxs;
		letter-spacing: 2px;
		max-width: 200px;
		font-size: $font-smd;
	}
}
.message {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 100%;
	background: $gray100;
	border-radius: $w-md;
	&-icon {
		width: 140px;
		height: 140px;
		background: $white;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		img {
			width: 90%;
			max-width: 100px;
		}
	}
	&-title {
		color: $primary-color;
		font-size: $font-md;
	}
	&-btn {
		font-size: 1.2rem;
		padding: 8px 32px;
		border-radius: 5px !important;
	}
	&[data-con="list"] {
		height: calc(100vh - 232px);
	}
}
