.header{
    background: $white;
    color: $primary-color;
    display: flex;
    justify-content: center;
    position: relative;
    padding: 14px 16px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    /*box-shadow: 0 0 8px $gray500;*/
    z-index: 100;
    &-title{
        font-weight: $font-bold;
    font-size: $font-md;
    }
    &-btn{
        position: absolute;
        right: 7px;
        top: 6px;
        padding: 0;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.detail-list{
    display: flex;
    flex-direction: column;
    &-item{
        width: 100%;
        margin-bottom: $w-xxs;
        &:last-child{
            margin-bottom: 0;
        }
    }
    &-btn{
        padding: 10px $w-sm;
        display: flex;
        background: $gray100;
        color: $gray800;
        border: 0 !important;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        border-radius: $w-xxxs;
        &.active{
            background: $primary-color;
            color: $white;
        }
        p{
            width: 100%;
            text-align: left;
        }
        span{
            font-size: $font-xsm;
            flex-shrink: 0;
            margin-left: $w-xs;
        }
    }
}
.notice{
    background: darken($color: $primary-xxlight-color, $amount: 2%);
    border-radius: $w-md;
    
    &-title{
        color: $primary-color;
        display: flex;
        align-items: center;
        font-size: $font-sm;
    }
    &-body{
        font-size: $font-xsm;
    }
}
.end-box{
    border-radius: $w-md $w-md 0 0;
    background: $white;
}
.highlight-box{
    background: $gray100;
    border-radius: $w-md;
    text-align: center;
    &[data-con="num"]{
        font-size: $font-xxl;
        font-weight: $font-heavy;
    color: $primary-color;
    }
    &-txt{
        font-weight: $font-bold;
        font-size: $font-xsm;
        color: $gray800;
        display: inline-block;
        li{
            display: flex;
            margin: 4px 0;
        }
        span{
            display: inline-block;
            min-width: 132px;
            text-align: right;
        }
    }
}

.main-btn{
    display: block;
    border-radius: 24px;
    background: $gray500;
    padding: 0 5px 5px 0;
    &-up{
        border-radius: 20px;
        background: $white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 32px 8px 16px 8px;
    }
    &[data-style="horizon"]{
        width: 100%;
        .main-btn-up{
            padding-top: 12px;
            padding-bottom: 12px;
            width: 100%;
        }
    }
    &-img{
        margin-bottom: 16px;
    }
    &-txt{
        color: $primary-light-color;
        font-size: $font-smd;
        font-weight: $font-xheavy;
        text-align: center;
    }
}
.main-message{
    text-align: center;
    color: $white;
    line-height: 1.6;
    font-size: $font-sm;
    font-weight: $font-heavy;
}
.main-container{
    background: $white;
    border-radius: 24px;
    padding: 16px 8px 24px 8px;
    width: 100%;
    display: flex;
    flex-direction: column;
    &-method{
        display: flex;
        justify-content: center;
    }
    &-number{
        display: flex;
        flex-direction: column;
        align-items: center;
        border-top: 1px solid $gray200;
        border-bottom: 1px solid $gray200;
        color: $primary-color;
        font-weight: $font-xheavy;
        padding: 16px 0;
        p{
            font-size: $font-md;
            span{
                font-size: $font-xxl;
            }
        }
    }
    &-txt{
        background: $primary-xxlight-color;
        border-radius: 50px;
        padding: 2px 16px;
        margin: 0 auto;
        color: $primary-color;
        font-weight: $font-xheavy;
        display: inline-block;
        margin: 0 auto;
    }
    &-pic{
        width: 215px;
        margin: 0 auto;
    }
}
.nav-grid{
    display: flex;
    margin-left: auto;
    padding: 0;
    &-link{
        background: none;
        border: none;
        opacity: 0.3;
        &.active{
            opacity: 1;
        }
    }
}